'use client';

import { useState, useEffect } from 'react';
import { useTheme } from '@/providers/theme-provider';
import { Box, IconButton, Menu, MenuItem, Tooltip } from '@mui/material';
import { LightMode, DarkMode, Settings } from '@mui/icons-material';
import '@/styles/theme-switcher.css';

export default function ThemeSwitcher({ showText = false }: { showText?: boolean }) {
  const { theme, setTheme } = useTheme();
  const [mounted, setMounted] = useState(false);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  // Only show the UI after mounting to avoid hydration mismatch
  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) {
    return null;
  }

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleThemeChange = (newTheme: 'light' | 'dark' | 'system') => {
    setTheme(newTheme);
    handleClose();
  };

  const getCurrentIcon = () => {
    if (theme === 'dark') {
      return <DarkMode className="theme-switcher-icon" />;
    }
    return <LightMode className="theme-switcher-icon" />;
  };

  return (
    <Box className="theme-switcher">
      <Tooltip title="Change theme">
        <IconButton
          onClick={handleClick}
          size="small"
          aria-controls={open ? 'theme-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
          color="inherit"
        >
          {getCurrentIcon()}
        </IconButton>
      </Tooltip>
      <Menu
        id="theme-menu"
        className="theme-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'theme-button',
        }}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <MenuItem 
          onClick={() => handleThemeChange('light')}
          className={`theme-menu-item ${theme === 'light' ? 'selected' : ''}`}
          sx={{ gap: 1 }}
        >
          <LightMode fontSize="small" /> 
          {showText !== false && 'Light'}
        </MenuItem>
        <MenuItem 
          onClick={() => handleThemeChange('dark')}
          className={`theme-menu-item ${theme === 'dark' ? 'selected' : ''}`}
          sx={{ gap: 1 }}
        >
          <DarkMode fontSize="small" /> 
          {showText !== false && 'Dark'}
        </MenuItem>
        <MenuItem 
          onClick={() => handleThemeChange('system')}
          className={`theme-menu-item ${theme === 'system' ? 'selected' : ''}`}
          sx={{ gap: 1 }}
        >
          <Settings fontSize="small" />
          {showText !== false && 'System'}
        </MenuItem>
      </Menu>
    </Box>
  );
} 